import { UserContext as UserContextType, statusTypes, UserProfile } from '../types'
import { useState, useMemo } from 'react'
import { UserContext } from './UserContext'
import { loginApi } from '../services/UserService'

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
export const UserProvider = (props: any) => {
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null)

  // TODO add the logic to handle API calls

  const updateUserProfile = (profile: UserProfile): void => {
    setUserProfile({ ...profile })
    // call the API to store the changes...
  }

  const logIn = (
    username: string,
    password: string,
    setStatus: (status?: statusTypes) => void,
  ): void => {
    loginApi(username, password)
      .then((user) => {
        setUserProfile(user)
        setStatus('success')
      })
      .catch((e) => {
        setStatus('error')
        throw e
      })
  }

  const logOut = (): void => {
    console.log('log out called')
    setUserProfile(null)
    // call the API to make the changes...
  }

  const acceptToS = (): void => {
    console.log('ToS called')
  }

  const checkUserRoles = (roleToCheck: string): boolean => {
    return Boolean(userProfile?.roles.find((role) => role === roleToCheck))
  }

  const memoValue = useMemo<UserContextType>(
    () => ({
      userProfile,
      logIn,
      logOut,
      updateUserProfile,
      acceptToS,
      checkUserRoles,
    }),
    [acceptToS, userProfile, logIn, logOut],
  )

  return <UserContext.Provider value={memoValue} {...props} />
}
