import { useSecurity } from '../../context/SecurityContext'
import { Guest } from '../../types'
import { InviteDataLine, Button, InputErrorMessage } from '../primitives'

export const GuestApprovalForm = ({ guestData }: { guestData: Guest }): JSX.Element => {
  const { authorizeGuest, authResult } = useSecurity()

  const handleOnClick = (dec: string): void => {
    authorizeGuest(dec)
  }
  return (
    <div>
      {(Object.keys(guestData) as Array<keyof typeof guestData>).map((k) => (
        <InviteDataLine key={k} label={k} value={guestData[k]} />
      ))}
      <div>
        <Button
          className='bg-green-600'
          onClick={() => {
            handleOnClick('success')
          }}
        >
          Accept
        </Button>
        <Button
          className='bg-red-600'
          onClick={() => {
            handleOnClick('fail')
          }}
        >
          Decline
        </Button>
      </div>
      {authResult != null && <InputErrorMessage>{authResult.result}</InputErrorMessage>}
    </div>
  )
}
