import { Input, InputLabel, InputErrorMessage } from './'
import { useField } from 'formik'
interface FormElementProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label: string
}

export const FormElement = ({ label, name, ...props }: FormElementProps): JSX.Element => {
  if (name === null || name === undefined) {
    return <></>
  }

  const [field, meta] = useField({ name, ...props })

  return (
    <div className='flex flex-col mb-1'>
      <InputLabel htmlFor={name}>{label}</InputLabel>
      <Input {...{ ...field, ...props }} />
      {meta.touched && meta.error != null ? (
        <InputErrorMessage>{meta.error}</InputErrorMessage>
      ) : null}
    </div>
  )
}
