import { Invite } from '../types'

export const addInviteApi = async (newInvite: Omit<Invite, 'id'>): Promise<Invite> => {
  // call the API with fetch and return a promise
  return await new Promise((resolve, reject) => {
    if (newInvite.location === 'casa' || newInvite.location === 'club') {
      resolve({ ...newInvite, id: Math.random().toString() })
    } else {
      reject(new Error('there was a problem with the invite creation'))
    }
  })
}
