interface LabelProps extends React.LabelHTMLAttributes<HTMLLabelElement> {}
export const InputLabel = ({ htmlFor, className, children, ...props }: LabelProps): JSX.Element => {
  return (
    <label
      htmlFor={htmlFor}
      className={`${className ?? ''} font-bold w-full capitalize`}
      {...props}
    >
      {children}
    </label>
  )
}
