import { Link as RouterLink, LinkProps } from 'react-router-dom'

export const Link = (props: LinkProps): JSX.Element => (
  <RouterLink
    {...props}
    className={`${
      props.className ?? ''
    } capitalize text-sm underline text-teal-600 underline-offset-4`}
  >
    {props.children}
  </RouterLink>
)
