interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> {}

export const Input = ({ name, type, className, ...props }: InputProps): JSX.Element => {
  return (
    <input
      name={name}
      type={type}
      className={`${className ?? ''} rounded-sm border p-1 border-gray-400`}
      {...props}
    />
  )
}
