import { Link, LinkProps } from 'react-router-dom'

export interface CardProps extends LinkProps {}

export const Card = ({ id, to, className, children }: CardProps): JSX.Element => {
  return (
    <Link
      to={to}
      className={`${
        className ?? ''
      } border-gray-300 block shadow-md active:shadow-xl border w-full p-6 rounded-lg`}
    >
      {children}
    </Link>
  )
}
