/* eslint-disable @typescript-eslint/no-empty-interface */
import { Formik, Form, FormikHelpers } from 'formik'
import { Button, FormElement, ContentBody, InputErrorMessage } from './primitives'
import * as Yup from 'yup'
import { onSubmitLogInProps, loginFormValues } from '../types'
import { useNavigate } from 'react-router-dom'

interface loginFormProps {
  onSubmit: (props: onSubmitLogInProps) => void
}

type handleOnSubmitType = (
  values: loginFormValues,
  formikHelpers: FormikHelpers<loginFormValues>,
) => void

export const LogInForm = ({ onSubmit }: loginFormProps): JSX.Element => {
  const navigate = useNavigate()
  const initialValues: loginFormValues = { username: '', password: '' }
  const validationSchema = Yup.object({
    username: Yup.string().max(15, 'Must be 15 characters or less').required('Required'),
    password: Yup.string().required('Required'),
  })

  const handleOnSubmit: handleOnSubmitType = (values, { setStatus, setSubmitting }) => {
    onSubmit({ values, setStatus })
    setSubmitting(false)
  }

  return (
    <ContentBody>
      <Formik
        initialValues={initialValues}
        onSubmit={handleOnSubmit}
        validationSchema={validationSchema}
      >
        {({ isSubmitting, status }) => {
          if (status === 'success') {
            navigate('/')
          } else {
            return (
              <Form>
                <FormElement label='username' name='username' />
                <FormElement label='password' name='password' type='password' />
                <Button type='submit' className='mt-9'>
                  Submit
                </Button>
                {status === 'error' && (
                  <InputErrorMessage>Please check your credentials and try again</InputErrorMessage>
                )}
              </Form>
            )
          }
        }}
      </Formik>
    </ContentBody>
  )
}
