import { createBrowserRouter, RouterProvider } from 'react-router-dom'
import { useUser } from './context/UserContext'
import { Root, ErrorPage, Dashboard, Invite, RootSecurity } from './routes'
import { LogIn } from './routes/login'

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
export const routerCreator = (isSecurity: boolean) =>
  createBrowserRouter([
    {
      path: '/',
      element: isSecurity ? <RootSecurity /> : <Root />,
      errorElement: <ErrorPage />,
      children: isSecurity
        ? []
        : [
            {
              path: '/',
              element: <Dashboard />,
            },
            {
              path: 'preview/:inviteId',
              element: <Invite mode='preview' />,
            },
            {
              path: '/new',
              element: <Invite mode='create' />,
            },
          ],
    },
    {
      path: '/login',
      element: <LogIn />,
    },
  ])

export const Router = (): JSX.Element => {
  const { checkUserRoles } = useUser()
  const isSecurity = checkUserRoles('security')
  return <RouterProvider router={routerCreator(isSecurity)} />
}
