import { InviteDataLine, InputLabel, GuestList } from './'
import { Invite } from '../../types.d'

export const RenderInvite = ({ invite }: { invite: Invite }): JSX.Element => {
  return (
    <>
      {(Object.keys(invite) as Array<keyof typeof invite>).map((k) => {
        if (k !== 'guests' && k !== 'id') {
          return <InviteDataLine key={k} label={k} value={invite[k]} />
        }
        if (k === 'guests') {
          return (
            <>
              <InputLabel>Guests</InputLabel>
              <GuestList guests={invite[k]} />
            </>
          )
        }
        return <></>
      })}
    </>
  )
}
