import { SecurityContext as SecurityContextType, Guest } from '../types'
import { useState, useMemo } from 'react'
import { SecurityContext } from './SecurityContext'
import { getGuestDataApi, authorizeGuestApi } from '../services/GuestService'

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
export const SecurityProvider = (props: any) => {
  const [localGuest, setLocalGuest] = useState<Guest>()
  const [error, setError] = useState<Error>()
  const [auth, setAuth] = useState<{ result: string }>()

  const getGuestById = (id: string): void => {
    getGuestDataApi(id)
      .then((guest) => {
        setLocalGuest(guest)
      })
      .catch((e) => {
        setError(e)
        throw e
      })
  }

  const authorizeGuest = (auth: string): void => {
    authorizeGuestApi(auth)
      .then((result) => {
        setAuth(result)
      })
      .catch((e) => {
        setError(e)
        throw e
      })
  }

  const memoValue = useMemo<SecurityContextType>(
    () => ({
      guest: localGuest,
      error,
      authResult: auth,
      getGuestById,
      authorizeGuest,
    }),
    [localGuest, getGuestById],
  )

  return <SecurityContext.Provider value={memoValue} {...props} />
}
