/* eslint-disable @typescript-eslint/strict-boolean-expressions */
import { useState } from 'react'
import { QrReader } from 'react-qr-reader'
import { useSecurity } from '../../context/SecurityContext'
import { InputErrorMessage } from '../primitives'

export const QRReader = (props: any): JSX.Element => {
  const [qrError, setQrError] = useState<Error>()
  const { error, getGuestById } = useSecurity()

  // TODO: type this correctly
  const handleResult = (result: any, error?: Error | null): void => {
    if (!(result == null)) {
      getGuestById(result.getText())
    }

    if (error !== null) {
      setQrError(error)
    }
  }

  return (
    <>
      <QrReader constraints={{ facingMode: 'environment' }} onResult={handleResult} />
      {qrError && <InputErrorMessage>{qrError?.message}</InputErrorMessage>}
      {error && <InputErrorMessage>{error.message}</InputErrorMessage>}
    </>
  )
}
