import React, { useState } from 'react'
import { Button, FormElement, H3 } from './primitives'
// import * as Yup from 'yup'
import { onSubmitAddGuestProps, Guest } from '../types.d'

interface newGuestProps {
  addGuests: (props: onSubmitAddGuestProps) => void
}
const emptyGuest = { firstName: '', email: '', lastName: '' }

const GuestForm = ({ addGuests }: newGuestProps): JSX.Element => {
  const [values, setValues] = useState<Guest>(emptyGuest)

  const handleOnClick = (e: React.FormEvent): void => {
    e.stopPropagation()
    if (values === emptyGuest) return
    addGuests({ values })
    setValues(emptyGuest)
  }

  const handleOnChange = (e: any): void => {
    const { name, value } = e.target
    setValues((prevState) => ({
      ...prevState,
      [name]: value,
    }))
  }

  return (
    <div id='guestSection' className='bg-gray-100 my-3 px-5 pt-3 pb-4'>
      <H3 className='mb-2'>Add Guest</H3>
      <FormElement
        id='firstName'
        name='firstName'
        type='text'
        onChange={handleOnChange}
        value={values.firstName}
        label='first name'
      />
      <FormElement
        id='lastName'
        name='lastName'
        type='text'
        onChange={handleOnChange}
        value={values.lastName}
        label='last name'
      />
      <FormElement
        label='email address'
        id='email'
        name='email'
        type='email'
        onChange={handleOnChange}
        value={values.email}
      />

      <Button type='button' onClick={handleOnClick}>
        Add Guest
      </Button>
    </div>
  )
}

export default GuestForm
