import { Guest } from '../types'

export const getGuestDataApi = async (id: string): Promise<Guest> => {
  // call the API with fetch and return a promise
  return await new Promise((resolve, reject) => {
    if (id === 'test123') {
      resolve({
        firstName: 'cosme',
        lastName: 'fulanito',
        email: 'cosmefulanito@gmail.com',
      })
    } else if (id === 'asd123') {
      console.log('here')
      resolve({
        firstName: 'guest',
        lastName: 'second',
        email: 'guardia1@gmail.com',
      })
    } else {
      reject(new Error('id was not found'))
    }
  })
}

export const authorizeGuestApi = async (auth: string): Promise<{ result: string }> => {
  // call the API with fetch and return a promise
  return await new Promise((resolve, reject) => {
    if (auth === 'success') {
      resolve({ result: 'guest is authorized' })
    } else if (auth === 'fail') {
      resolve({ result: 'guest is unauthorized' })
    } else {
      reject(new Error('auth was not performed'))
    }
  })
}
