/* eslint-disable @typescript-eslint/no-empty-interface */
import { useState } from 'react'
import { Formik, Form, FormikHelpers } from 'formik'
import { Button, FormElement, ContentBody, InputErrorMessage, GuestList } from './primitives'
import * as Yup from 'yup'
import { InviteFormValues, onSubmitNewInviteProps, Guest, onSubmitAddGuestProps } from '../types.d'
import { useNavigate } from 'react-router-dom'
import GuestForm from './GuestForm'

type handleOnSubmitType = (
  values: InviteFormValues,
  formikHelpers: FormikHelpers<InviteFormValues>,
) => void

interface newInviteFormProps {
  onSubmit: (props: onSubmitNewInviteProps) => void
}

export const NewInviteForm = ({ onSubmit }: newInviteFormProps): JSX.Element => {
  const initialValues: InviteFormValues = { location: '', date: '', time: '', guests: [] }
  const validationSchema = Yup.object({
    location: Yup.string().max(15, 'Must be 15 characters or less').required('Required'),
    date: Yup.date().required('Required'),
    time: Yup.string().required('Required'),
  })
  const [guests, setGuests] = useState<Guest[]>([])
  const navigate = useNavigate()
  const handleOnSubmit: handleOnSubmitType = (values, { setStatus, setSubmitting }) => {
    onSubmit({ values: { ...values, guests }, setStatus })
    setSubmitting(false)
  }

  const addGuests: (props: onSubmitAddGuestProps) => void = ({ values }) => {
    console.log(guests)
    setGuests((prev) => [...prev, values])
  }

  const deleteGuest = (guest: Guest): void => {
    // someArray = someArray.filter(function(e) { return e.Name !== "Kristian"; });
    setGuests((g) =>
      g.filter((e) => {
        return e !== guest
      }),
    )
  }

  return (
    <ContentBody>
      <Formik
        initialValues={initialValues}
        onSubmit={handleOnSubmit}
        validationSchema={validationSchema}
      >
        {({ isSubmitting, status }) => {
          if (status === 'success') {
            navigate('/')
          } else {
            return (
              <Form>
                <FormElement label='location' name='location' />
                <FormElement label='date' name='date' type='date' />
                <FormElement label='time' name='time' type='time' />
                <div>
                  <p>invited Guests</p>
                  <div>
                    {guests.length > 0 ? (
                      <GuestList guests={guests} onDelete={deleteGuest} />
                    ) : (
                      <p> no guests yet </p>
                    )}
                  </div>
                </div>
                <GuestForm addGuests={addGuests} />
                <Button type='submit' className='mt-9'>
                  Submit
                </Button>
                {status === 'error' && (
                  <InputErrorMessage>
                    There was a problem with the invite creation
                  </InputErrorMessage>
                )}
              </Form>
            )
          }
        }}
      </Formik>
    </ContentBody>
  )
}
