import { Invite, InvitesContext as InvitesContextType, statusTypes } from '../types'
import { useState, useMemo } from 'react'
import { InvitesContext } from './InvitesContext'
import { addInviteApi } from '../services/InvitesService'

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
export const InvitesProvider = (props: any) => {
  const [localInvites, setLocalInvites] = useState<Invite[]>([])

  const addInvite = (
    newInvite: Omit<Invite, 'id'>,
    setStatus: (status?: statusTypes) => void,
  ): void => {
    console.log('localInvites', localInvites)
    addInviteApi(newInvite)
      .then((invite) => {
        setInvites([invite])
        setStatus('success')
      })
      .catch((e) => {
        setStatus('error')
        throw e
      })
  }

  const setInvites = (invites: Invite[]): void => {
    setLocalInvites((i) => [...i, ...invites])
  }

  const memoValue = useMemo<InvitesContextType>(
    () => ({
      invites: localInvites,
      addInvite,
      setInvites,
    }),
    [localInvites, addInvite, setInvites],
  )

  return <InvitesContext.Provider value={memoValue} {...props} />
}
