import { NewInviteForm } from '../components/NewInviteForm'
import { useParams } from 'react-router-dom'
import { H1, RenderInvite, ContentHeader, ContentBody } from '../components/primitives'
import { onSubmitNewInviteProps } from '../types.d'
import { useInvites } from '../context/InvitesContext'

type OnSubmitType = (props: onSubmitNewInviteProps) => void

export const Invite = ({ mode }: { mode: 'create' | 'preview' }): JSX.Element => {
  const { invites, addInvite } = useInvites()
  const { inviteId } = useParams()
  const currentInvite = invites.find((e) => e.id === inviteId)

  const onSubmit: OnSubmitType = ({ values, setStatus }) => {
    console.log('submitting', values)
    addInvite(values, setStatus)
  }

  return (
    <>
      {mode === 'preview' ? (
        <>
          <ContentHeader className='grid-cols-1'>
            <H1>{`Invitation ${inviteId ?? ''}`}</H1>
          </ContentHeader>
          <ContentBody>
            {currentInvite != null && <RenderInvite invite={currentInvite} />}
          </ContentBody>
        </>
      ) : (
        <>
          <ContentHeader className='grid-cols-1'>
            <H1>Create new Invitation</H1>
          </ContentHeader>
          <NewInviteForm onSubmit={onSubmit} />
        </>
      )}
    </>
  )
}
