import { InvitesContext as InvitesContextType } from '../types'
import { createContext, useContext } from 'react'

export const InvitesContext = createContext<InvitesContextType>({
  invites: [],
  addInvite: () => {},
  setInvites: () => {},
})

export const useInvites = (): InvitesContextType => {
  const context = useContext(InvitesContext)

  // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
  if (!context) {
    throw new Error('useCurrentInvites must be used within a UserProvider')
  }
  return context
}
