import { H1, LinkButton, ContentHeader, ContentBody } from '../components/primitives'
import { InviteDashboardCard } from '../components/InviteDashboardCard'
import { useInvites } from '../context/InvitesContext'

export const Dashboard = (): JSX.Element => {
  const { invites } = useInvites()

  return (
    <>
      <ContentHeader>
        <H1>Dashboard</H1>
        <LinkButton to='/new'>+ New invite</LinkButton>
      </ContentHeader>
      <ContentBody>
        {invites.length === 0 && (
          <span>No invites yet. Please add new invites using the + New Invite button</span>
        )}
        {invites.map((invite, key) => (
          <InviteDashboardCard
            className='first:mt-0 mt-4 mb-4 last:mb-0'
            key={invite.id}
            invite={invite}
          />
        ))}
      </ContentBody>
    </>
  )
}
