import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { NavBar } from '../../components/primitives/NavBar'
import { PageContainer } from '../../components/primitives'
import { useUser } from '../../context/UserContext'
import { useSecurity } from '../../context/SecurityContext'

import { GuestApprovalForm } from '../../components/security/GuestApprovalForm'
import { QRReader } from '../../components/security/QRReader'

const Root: React.FC = () => {
  const navigate = useNavigate()
  const { guest } = useSecurity()

  const { userProfile, checkUserRoles, logOut } = useUser()
  useEffect(() => {
    const isSecurity: boolean = checkUserRoles('security')

    if (userProfile === null) {
      navigate('/login')
    }
    if (!isSecurity) {
      logOut()
      navigate('/login')
    }
  }, [userProfile])

  return (
    <PageContainer id='root-page-container'>
      <NavBar NavBarElements={[]} />
      <div id='detail-container' className='w-full h-full p-4'>
        {guest === undefined ? <QRReader /> : <GuestApprovalForm guestData={guest} />}
      </div>
    </PageContainer>
  )
}

export default Root
