import { H1, ContentHeader, ContentBody, PageContainer } from '../components/primitives'
import { LogInForm } from '../components/LogInForm'
import { useUser } from '../context/UserContext'
import { useNavigate } from 'react-router-dom'
import { useEffect } from 'react'
import { onSubmitLogInProps } from '../types'

type OnSubmitType = (props: onSubmitLogInProps) => void
export const LogIn = (): JSX.Element => {
  const navigate = useNavigate()
  const { userProfile, logIn } = useUser()

  const onSubmit: OnSubmitType = ({ values, setStatus }) => {
    const { username, password } = values
    password != null && logIn(username, password, setStatus)
  }

  useEffect(() => {
    if (userProfile !== null) {
      navigate('/')
    }
  }, [userProfile])

  return (
    <PageContainer id='login-page-container' className='px-4'>
      <ContentHeader>
        <H1>Welcome to EasyCountries</H1>
      </ContentHeader>
      <ContentBody>
        <LogInForm onSubmit={onSubmit} />
      </ContentBody>
    </PageContainer>
  )
}
