import { Button, Link } from '.'
import { Link as RouterLink, LinkProps, useLocation } from 'react-router-dom'
import { useUser } from '../../context/UserContext'

export const NavBar = ({ NavBarElements = [] }: { NavBarElements: LinkProps[] }): JSX.Element => {
  const location = useLocation()
  const { userProfile, logOut } = useUser()

  return (
    <div id='nav-container' className='w-full  bg-gray-100 p-4 shadow-lg columns-3'>
      <div id='nav-left' className='w-full h-full'>
        <RouterLink to='/'>
          <span>EasyCountries</span>
        </RouterLink>
      </div>
      <div className='w-full h-full'>
        {NavBarElements?.length > 0 && (
          <nav id='nav-bar'>
            <ul className='h-full w-full ml-auto align-center'>
              {NavBarElements.map((e) => {
                if (location.pathname !== e.to) {
                  if (location.pathname !== '/') {
                    return (
                      <li key={e.id} className='text-right'>
                        <Link {...e} className='block'>
                          {e.children}
                        </Link>
                      </li>
                    )
                  }
                }
                return <></>
              })}
            </ul>
          </nav>
        )}
      </div>
      <div className='w-full text-right'>
        <span className='text-cyan-800 italic'>Hi, {userProfile?.firstName} </span>
        <Button onClick={logOut}>LogOut</Button>
      </div>
    </div>
  )
}
