export const H1 = ({ children }: { children: React.ReactNode }): JSX.Element => (
  <h1 className='text-lg font-semibold text-gray-700'>{children}</h1>
)

export const H3 = (props: React.HTMLAttributes<HTMLHeadingElement>): JSX.Element => (
  <h3 {...props} className={`${props.className ?? ''} font-bold`}>
    {props.children}
  </h3>
)
