import React from 'react'

export const PageContainer = (props: React.HTMLAttributes<HTMLDivElement>): JSX.Element => {
  return (
    <div
      {...props}
      className={`${
        props.className ?? ''
      } md:w-96 overflow-hidden bg-white container md:border mx-auto h-screen flex flex-col font-normal text-base`}
    >
      {props.children}
    </div>
  )
}
