import React, { useEffect } from 'react'
import { Outlet, useNavigate } from 'react-router-dom'
import { NavBar } from '../components/primitives/NavBar'
import { PageContainer } from '../components/primitives'
import { useUser } from '../context/UserContext'

const Root: React.FC = () => {
  const navigate = useNavigate()
  const { userProfile, checkUserRoles, logOut } = useUser()

  useEffect(() => {
    const isDefault: boolean = checkUserRoles('default')

    if (userProfile === null) {
      navigate('/login')
    }
    if (!isDefault) {
      logOut()
      navigate('/login')
    }
  }, [userProfile])
  return (
    <PageContainer id='root-page-container'>
      <NavBar
        NavBarElements={
          [
            // {
            //   to: '/',
            //   children: 'go to dashboard',
            // },
          ]
        }
      />
      <div id='detail-container' className='w-full h-full p-4 pb-5 overflow-auto'>
        <Outlet />
      </div>
    </PageContainer>
  )
}

export default Root
