import { Card, CardProps, RenderInvite } from './primitives'
import { Invite } from '../types.d'

interface InviteDashboardCardProps extends Partial<CardProps> {
  invite: Invite
}

export const InviteDashboardCard = ({
  invite,
  id,
  ...props
}: InviteDashboardCardProps): JSX.Element => {
  return (
    <Card id={id} to={`/preview/${invite.id}`} {...props}>
      <RenderInvite invite={invite} />
    </Card>
  )
}
