import { Guest } from '../../types.d'
export const GuestList = ({
  guests,
  onDelete,
}: {
  guests: Guest[]
  onDelete?: (guest: Guest) => void
}): JSX.Element => {
  return (
    <>
      {guests.map((guest, key) => (
        <div
          key={`${guest.firstName}${key}`}
          className={`flex mb-1 py-2 px-3 pr-1 align-baseline
           ${onDelete != null ? 'shadow-sm shadow-gray-300' : 'border-b-2 last:border-0'}`}
        >
          <p className='mr-1'>{guest.firstName} </p>
          <p>{guest.lastName}</p>
          {onDelete != null && (
            <button
              type='button'
              onClick={() => {
                onDelete(guest)
              }}
              className='pt-0 px-3 ml-auto pb-2 font-bold text-gray-500'
            >
              x
            </button>
          )}
        </div>
      ))}
    </>
  )
}
