import { Link, LinkProps } from 'react-router-dom'

export const LinkButton = (props: LinkProps): JSX.Element => {
  return (
    <Link
      {...props}
      className={`${
        props.className ?? ''
      } text-gray-700 p-1 text-center bg-gray-100 center rounded-sm shadow-sm`}
    >
      {props.children}
    </Link>
  )
}
